h1 {
    margin-top: 8px;
    font-size: 28px;
    color: white;
    font-weight: 600;
    background-color: rgba(0, 0, 0, 0.377);
    box-shadow: -1px 1px 5px 8px rgba(0, 0, 0, 0.253);
    border-radius: 12px;
    padding-bottom: 12px;
  }
  
  .coinCard {
    width: 550px;
    border-radius: 12px;
    color: white;
    box-shadow: 1px 1px 2px 3px rgba(0, 0, 0, 0.171);
    margin: 0 20px 20px 20px;
  }
  
  .cardTopInfo {
    border: 1px solid rgba(255, 255, 255, 0.274);
    border-radius: 12px;
    margin: 24px;
  }
  
  .row {
    display: flex;
  }
  
  .rightInfo p,
  .rightInfo span {
    margin: 16px;
  }
  
  .greyInfo {
    color: rgb(173, 173, 173);
    text-shadow: 1px 1px 2px rgb(0, 0, 0);
  }
  
  .coinInfo {
    border-left: 1px solid rgba(255, 255, 255, 0.342);
  }
  
  .coinInfo img {
    height: 50px;
    width: 50px;
    margin: 16px;
    padding-left: 16px;
  }
  
  .coinInfo p {
    margin-top: 14px;
    font-size: 20px;
  }
  
  .marginTop {
    margin-top: 10px;
  }
  
  .calculatedInfoBottom div {
    width: 40%;
    background-color: #2e3944;
    height: 80px;
    margin: 20px;
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.103);
    text-align: center;
  }
  
  .calculatedInfoBottom div span {
    margin-top: 8px;
  }
  
  .slider {
    width: 85%;
    height: 10px;
    margin-left: 40px;
    cursor: pointer;
  }
  
  .sliker::-webkit-slider-runnable-track {
    height: 10px;
    background: #405061;
    border-radius: 4px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.15);
  }
  
  .slider::-webkit-slider-thumb {
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    margin-top: -3px;
    background: #d2d3d4;
  }
  
  /* .description */
  .description {
    margin: 12px;
  }
  
  .loading {
    color: white;
    font-size: 28px;
    margin-top: 100px;
  }
  
  /* .buttons-block */
  .buttonsBlock {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .btn {
    background-color: #3b474d;
    color: white;
    border-radius: 100px;
    box-shadow: rgba(74, 130, 134, 0.2) 0 -25px 18px -14px inset, rgba(44, 118, 187, 0.15) 0 1px 2px, rgba(74, 103, 107, 0.15) 0 2px 4px, rgba(61, 103, 110, 0.226) 0 4px 8px, rgba(54, 112, 134, 0.15) 0 8px 16px, rgba(41, 74, 102, 0.15) 0 16px 32px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    transition: all 250ms;
    border: 0;
    font-size: 16px;
    width: 190px;
    height: 50px;
    margin: 0px 50px 20px 40px;
  }
  
  .btn:hover {
    box-shadow: rgba(44, 130, 187, 0.35) 0 -25px 18px -14px inset, rgba(158, 175, 180, 0.25) 0 1px 2px, rgba(189, 194, 196, 0.25) 0 2px 4px, rgba(201, 206, 211, 0.25) 0 4px 8px, rgba(44, 170, 187, 0.25) 0 8px 16px, rgba(44, 170, 187, 0.25) 0 16px 32px;
    transform: scale(1.05) rotate(-1deg);
  }
  
  .btn[disabled] {
    background-color: #26292b;
    pointer-events: none;
  }
  
  .rangeLabels {
    display: flex;
    justify-content: space-between;
    padding: 0 42px;
  }
  