.newsBlock{
    color: white;
    overflow: auto;
    height: 100%;
}

::-webkit-scrollbar-thumb {
    background: #2b3438;
  }

.newsItem{
    display: flex;
    margin-bottom: 20px;
    border-bottom: 1px solid #ddd;
    padding-bottom: 20px;
    margin:8px;
}

.newsItem a{
    font-size: 12px;
    border-bottom: 1px solid white;
}
.newsHeading{
    margin-left: 30px;
}

.newsImage{
    width: 30%;
    margin-right: 20px;
    border-radius: 12px;
}

.newsImage img {
    height: 160px;
    width: 220px;
    object-fit: cover; 
  }

  .newsTitle {
    font-size: 16px;
    font-weight: bold;
    margin-top: 0;
    margin-bottom: 10px;
  }

  .newsDescription {
    font-size: 14px;
    line-height: 1.3;
    margin-bottom: 10px;
  }
  
  .newsDate {
    font-size: 12px;
    color: #999;
  }